.progress-list__item {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 70px;
  margin-bottom: 43px;
  min-height: 39px;
}
.progress-list__item:last-child {
  margin-bottom: 0;
}
.progress-list__value {
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;
}
.progress-list__value:hover {
  cursor: pointer;
}
.progress-list__checkbox[type="checkbox"] + .checkbox-indicator {
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -19px;
  display: block;
  width: 39px;
  height: 39px;
  border: 2px solid #e5e5e5;
  border-radius: 2px;
  background-color: #ffffff;
}
.progress-list__checkbox[type="checkbox"]:checked + .checkbox-indicator::before {
  content: "";
  position: absolute;
  bottom: 16px;
  left: 12px;
  display: block;
  width: 20px;
  height: 2px;
  background-color: #d22856;
  transform: rotate(-45deg);
}
.progress-list__checkbox[type="checkbox"]:checked + .checkbox-indicator::after {
  content: "";
  position: absolute;
  bottom: 12px;
  left: 7px;
  display: block;
  width: 10px;
  height: 2px;
  background-color: #d22856;
  transform: rotate(45deg);
}
.progress-list__checkbox[type="checkbox"]:hover + .checkbox-indicator,
.progress-list__checkbox[type="checkbox"]:focus + .checkbox-indicator {
  border-color: #283645;
  transition: 0.2s;
}
.progress-list__checkbox[type="checkbox"]:active + .checkbox-indicator {
  border-color: #d22856;
  transition: 0.1s;
}
